const Card = styled.div`
  display: inline-block;
  color: white;
  margin-left: 8px;
  margin-right: 8px;
  border-radius: 4px;
  padding: 40px 8px;
  background: ${props => props.color};
  text-align: center;
  font-size: 32px;
  cursor: pointer;
  font-weight: 300;
  line-height: 1;
  width: 84px;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.035), 0 4px 8px rgba(0, 0, 0, 0.07);
`;

const Tray = styled.div`
  display: block;
  position: fixed;
  white-space: nowrap;
  overflow-x: auto;
  background: #063651;
  padding: 16px;
  left: 0;
  right: 0;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: -ms-autohiding-scrollbar;
`;

const ChevronContainer = styled.div`
  display: block;
  cursor: pointer;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 8px;
  text-align: right;
`;

const OpenClose = ({ open, onClick }) => (
  <ChevronContainer onClick={onClick}>
    <img
      src={
        open
          ? "https://icon.now.sh/chevronDown/CCC"
          : "https://icon.now.sh/chevronUp/CCC"
      }
      alt="down icon"
    />
  </ChevronContainer>
);

const Container = styled.div`
  font-family: "Hind", sans-serif;
  height: 100%;
`;

const colors = ["#E05557", "#FFBA00", "#00B6F0"];

const Stage = styled.div`
  color: ${props => props.color};
  margin: auto;
  font-weight: 600;
  font-size: ${props =>
    props.display === "Coffee" || props.display === "Defer" ? "5rem" : "12rem"};
`;

const Wrapper = styled.div`
  display: flex;
  background: #092e41;
  position: relative;
  align-items: center;
  height: ${props => (!props.open ? "100%" : "calc(100% - 144px)")};
`;

const Intro = styled.p`
  margin: auto;
  opacity: 0.5;
  color: white;
`;

export default class extends Component {
  state = {
    display: null,
    open: true,
    color: null
  };

  render() {
    const { display, color, open } = this.state;
    return (
      <Container>
        <Wrapper open={open}>
          {display === null ? (
            <Intro>Tap a Card Below</Intro>
          ) : (
            <Stage color={color} display={display}>
              {display}
            </Stage>
          )}
          <OpenClose
            open={open}
            onClick={() => this.setState({ open: !open })}
          />
        </Wrapper>
        {open && (
          <Tray>
            <Card
              color={colors[0]}
              onClick={() => this.setState({ display: 0, color: colors[0] })}
            >
              0
            </Card>
            <Fib
              color={colors[1]}
              number={1}
              onClick={n => this.setState({ display: n, color: colors[1] })}
            />
            <Fib
              color={colors[2]}
              number={2}
              onClick={n => this.setState({ display: n, color: colors[2] })}
            />
            <Fib
              color={colors[0]}
              number={3}
              onClick={n => this.setState({ display: n, color: colors[0] })}
            />
            <Fib
              color={colors[1]}
              number={4}
              onClick={n => this.setState({ display: n, color: colors[1] })}
            />
            <Fib
              color={colors[2]}
              number={5}
              onClick={n => this.setState({ display: n, color: colors[2] })}
            />
            <Fib
              color={colors[0]}
              number={6}
              onClick={n => this.setState({ display: n, color: colors[0] })}
            />
            <Fib
              color={colors[1]}
              number={7}
              onClick={n => this.setState({ display: n, color: colors[1] })}
            />
            <Fib
              color={colors[2]}
              number={8}
              onClick={n => this.setState({ display: n, color: colors[2] })}
            />
            <Fib
              color={colors[0]}
              number={9}
              onClick={n => this.setState({ display: n, color: colors[0] })}
            />
            <Fib
              color={colors[1]}
              number={10}
              onClick={n => this.setState({ display: n, color: colors[1] })}
            />
            <Fib
              color={colors[2]}
              number={11}
              onClick={n => this.setState({ display: n, color: colors[2] })}
            />
            <Card
              color={colors[0]}
              onClick={() => this.setState({ display: "?", color: colors[0] })}
            >
              ?
            </Card>
            <Card
              color={colors[1]}
              onClick={() => this.setState({ display: "∞", color: colors[1] })}
            >
              ∞
            </Card>
            <Card
              color={colors[2]}
              onClick={() =>
                this.setState({ display: "Defer", color: colors[2] })
              }
            >
              ➳
            </Card>
            <Card
              color={colors[0]}
              onClick={() =>
                this.setState({ display: "Coffee", color: colors[0] })
              }
            >
              <Coffee
                style={{ margin: "-5px 0 -5px", display: "inline-block" }}
                alt="coffee pot"
              />
            </Card>
          </Tray>
        )}
      </Container>
    );
  }
}



